import { Slot } from '@radix-ui/react-slot'
import { clsx } from 'clsx'
import { ButtonHTMLAttributes, ReactNode } from 'react'

type ButtonProps = ButtonHTMLAttributes<HTMLButtonElement> & {
  children: ReactNode,
  asChild?: boolean,
}

const Button: React.FC<ButtonProps> = ({ children, asChild = false, className, ...rest }: ButtonProps) => {
  const Comp = asChild ? Slot : 'button'

  return (
    <Comp
      className={clsx(
        'py-3 px-4 bg-cyan-500 rounded font-semibold text-black text-sm w-full transition-colors hover:bg-cyan-300 focus:ring-2 ring-white',
        className,
      )}
      {...rest}>
      {children}
    </Comp>
  )
}

export type { ButtonProps }
export { Button }

